import React, { useEffect, useState } from "react";
import { v4 as uuidv4 } from "uuid";
import { Modal } from "@mui/material";
import { Box } from "@mui/material";
import { del, getIR } from "../utils/methods";
import Loader from "./Loader";
import { Link } from "react-router-dom";
export default function Table({ data, onDelete }) {
  const [datas, setDatas] = useState([]); // our datas
  const [id, setId] = useState(""); // when we use this state, we want to remove a data by its id
  // these states responsible for handling remove one data >>
  const [removeStatus, setRemovestatus] = useState(false); //  does data is remove ? for handling remove status
  const [showDeleteAlert, setShowDeleteAlert] = useState(false); // modal -> yes or no for delete a data
  const [resultAlert, setResultAlert] = useState(false); // this is for showing result of delete a data in the modal
  // these states responsible for handling remove all data >>
  const [removeAllStatus, setRemovAllestatus] = useState(false); // do datas are remove ? for handling remove status
  const [deleteAllAlert, setDeleteAllAlert] = useState(false); // modal -> yes or no for delete all data
  const [resultAllDelete, setResultAllDelete] = useState(false); // this is for showing result of delete all data in the modal
  //_____
  const [showLoader, setShowLoader] = useState(false); // loading process
  // is remove ? >>
  // is remove all >>

  // geting data from  Table({ (((data))), onDelete }) at line (9)
  useEffect(() => {
    setDatas(data); //data is a parametr
  }, [data]);


  async function removeHandler() {
    // this process is for deleting (one item) of data base >>
    setShowLoader(true); // -> when showLoader is true -> render Loader component at line (30)
    try {
      const response = await del(`financeData/${id}`);
      setRemovestatus(response.ok);
    } catch (err) {
      setRemovestatus(false);
      console.log(err);
    }

    setTimeout(() => {
      // setTimeout is for 300ms waiting then unmount The Loader component and show delete result
      setShowLoader(false);
      setResultAlert(true);
    }, 300);
    setShowDeleteAlert(false); // this setState is for unmounting The delet Modal componenet
    onDelete(); // for real time and reFetch at -> Container/HomeContainer.js
  }

  async function removeAll() {
    setShowLoader(true); // -> when showLoader is true -> render Loader component at line (30)
    try {
      const response = await del(`financeData`);
      setRemovAllestatus(response.ok); // remove all is ok ?
    } catch (err) {
      setRemovAllestatus(false);
      console.log(err);
    }
    setShowLoader(false); // -> when showLoader is true -> render Loader component at line (30)
    setDeleteAllAlert(false); // this is for unmounting modal -> (yes or no)
    setResultAllDelete(true); // -> this is for showing result of delete all in the Modal

    onDelete(); // for real time and reFetch at -> Container/HomeContainer.js
  }
  return (
    <>
      <Loader isload={showLoader} width={!removeStatus ? "40%" : "100%"} />
      <div className=" flex justify-end px-5">
        <button
          onClick={() => setDeleteAllAlert(true)}
          className="btn-red my-5 relative"
        >
          حذف همه
        </button>
      </div>
      <div className="md:p-5">
        <div className="relative overflow-x-auto shadow-md sm:rounded-lg">
          <table className="w-full text-sm text-left rtl:text-right text-gray-500 dark:text-gray-400">
            <thead className="text-xs text-gray-700 uppercase bg-gray-50 dark:bg-gray-700 dark:text-gray-400">
              <tr className="text-white">
                <th scope="col" className="px-6 py-3">
                  مبلغ
                </th>
                <th scope="col" className="px-6 py-3">
                  بانک
                </th>
                <th scope="col" className="px-6 py-3">
                  تاریخ
                </th>
                <th scope="col" className="px-6 py-3">
                  ساعت
                </th>
                <th scope="col" className="px-6 py-3">
                  دلیل
                </th>
                <th scope="col" className="px-1 py-3"></th>
                <th scope="col" className="px-1 py-3"></th>
              </tr>
            </thead>
            <tbody>
              {datas.map((echData) => {
                return (
                 

                  
                  <tr
                    key={uuidv4()}
                    className={`${echData[1].isBuy ? "success" : "danger"}  
 text-white border-b dark:border-gray-700 border-gray-200 w-full`}
                  >
                    <td className="px-6 py-3">{getIR(echData[1].price)}</td>
                    <td className="px-6 py-3">{echData[1].card}</td>
                    <td className="px-6 py-3">{echData[1].date}</td>
                    <td className="px-6 py-3">{echData[1].time}</td>
                    <td className="px-6 py-3">{echData[1].reason}</td>
                    <td className="px-1 py-4">
                      <Link
                        to={`/update/${encodeURIComponent(
                          JSON.stringify(echData)
                        )}`}
                        className="btn-green"
                      >
                        ویرایش
                      </Link>
                    </td>
                    <td className="px-6 py-4">
                      <button
                        onClick={() => {
                          // show -> modal (yes or no) for delete process
                          setShowDeleteAlert(true);
                          setId(echData[0]);
                        }}
                        className="btn-red"
                      >
                        حذف
                      </button>
                    </td>
                  </tr>
                 
                );
              })}
            </tbody>
          </table>
        </div>
      </div>

      {/* this modal is for delete a data  >> */}
      <Modal
        open={showDeleteAlert}
        onClose={() => setShowDeleteAlert(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        className="flex justify-center items-center px-1"
      >
        <Box className="bg-gray-800 max-w-sm p-3 rounded-lg flex justify-center items-center flex-col">
          <span className="block text-xl text-gray-100">
            از پاک کردن این مورد مطمهنی ؟
          </span>
          <div className="p-5 flex items-center gap-x-3">
            <button
              onClick={removeHandler}
              className="success p-3 rounded-lg text-gray-100"
            >
              اره پاکش کن !
            </button>
            <button
              onClick={() => setShowDeleteAlert(false)}
              className="danger p-3 rounded-lg text-gray-100"
            >
              نه نه پاک نکن
            </button>
          </div>
        </Box>
      </Modal>
      {/* result of delete a data in this modal >> */}
      <Modal
        open={resultAlert}
        onClose={() => setResultAlert(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        className="flex justify-center items-center px-1"
      >
        <Box className="bg-gray-800 max-w-sm p-3 rounded-lg flex justify-center items-center flex-col text-xl [&>span]:block">
          {removeStatus ? (
            <span className="green">پاک شد !</span>
          ) : (
            <span className="red">پاک نشد, به علیرضا بگو !</span>
          )}
        </Box>
      </Modal>

      {/* this modal is for delete all (yes or no)  */}
      <Modal
        open={deleteAllAlert}
        onClose={() => setDeleteAllAlert(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        className="flex justify-center items-center px-1"
      >
        <Box className="bg-gray-800 max-w-sm p-3 rounded-lg flex justify-center items-center flex-col">
          <span className="block text-xl text-gray-100">
            داری همه اطلاعات را پاک میکنی مطمهنی ؟
          </span>
          <div className="p-5 flex items-center gap-x-3">
            <button
              onClick={removeAll}
              className="success p-3 rounded-lg text-gray-100"
            >
              اره پاکشون کن !
            </button>
            <button
              onClick={() => setDeleteAllAlert(false)}
              className="danger p-3 rounded-lg text-gray-100"
            >
              نه نه پاک نکن
            </button>
          </div>
        </Box>
      </Modal>
      {/* result of delete All data in this modal >> */}
      <Modal
        open={resultAllDelete}
        onClose={() => setResultAllDelete(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        className="flex justify-center items-center px-1"
      >
        <Box className="bg-gray-800 max-w-sm p-3 rounded-lg flex justify-center items-center flex-col text-xl [&>span]:block">
          {removeAllStatus ? (
            <span className="green">پاک شدن !</span>
          ) : (
            <span className="red">پاک نشد, به علیرضا بگو !</span>
          )}
        </Box>
      </Modal>
    </>
  );
}
