async function get(path) {
  return await fetch(
    `https://finance-f121c-default-rtdb.firebaseio.com/${path}.json`
  );
}
async function post(path, data) {
  return await fetch(
    `https://finance-f121c-default-rtdb.firebaseio.com/${path}.json`,
    {
      method: "POST",
      body: JSON.stringify(data),
    }
  );
}
async function del(id) {
  return await fetch(
    `https://finance-f121c-default-rtdb.firebaseio.com/${id}.json`,
    {
      method: "DELETE",
    }
  );
}
async function put(path, data) {
  return await fetch(
    `https://finance-f121c-default-rtdb.firebaseio.com/${path}.json`,
    {
      method: "put",
      body: JSON.stringify(data),
    }
  );
}
// Helper ---------------------------------------->>
// this function is for geting 10000 and convert it to "10,000"
const getIR = (value) => {
  if(!value || value==""){
    return "0"
  }
  let rawValue = value.replace(/[٬,]/g, "");
    if (!rawValue) return ""; // if value is a "" or any somthings false, return ""
    return new Intl.NumberFormat("fa-IR").format(rawValue); // for example -> 10000 convert to "10,000"
};

export { get, post, del, put }; // fetching methods
export { getIR }; // helper
