import React, { useEffect, useState } from "react";
import Home from "../pages/home/Home";
import { get } from "../utils/methods";
export default function HomeContainer() {
  const [updator, setUpdate] = useState(false); // responsible for rerender
  const [getStatus, setGetStatus] = useState(false);
  const [datas, setData] = useState([]);
  const [isComplete, setComplete] = useState(false);
  function reRender() {
    setUpdate((prev) => !prev);
  }
  async function fetchData() {
    try {
      const response = await get("financeData"); // waiting
      const status = response.ok;
      if (!status) return; //
      const jsonData = await response.json(); // waiting
      if (!jsonData) setData([]);
      else {
        const data = Object.entries(jsonData);
        setData(data);
      }
      setGetStatus(status);
    } catch (err) {
      setGetStatus(false); //اینجا معلوم کردم که فچ درست کار نکرده و میشه روش مدریت کرد درسته ؟؟؟
      console.log(err);
    } finally {
      setComplete(true);
    }

    // await get("financeData")
    //   .then((response) => {
    //     setGetStatus(response.ok);
    //     console.log(response.ok);
    //     return response.json();
    //   })
    //   .then((data) =>
    //     !Object.entries(data).length
    //       ? setData([])
    //       : setData(Object.entries(data))
    //   )
    //   .catch((err) => console.log(err));
  }
  useEffect(() => {
    fetchData();
  }, [updator]);
  return (
    <Home
      data={datas}
      dataStatus={getStatus}
      loader={isComplete}
      onDleteItem={reRender}
    />
  );
}
