import React, { useEffect, useState } from "react";
import { put } from "../../utils/methods";
import { useParams } from "react-router-dom";
import { Box } from "@mui/material";
import { Modal } from "@mui/material";
import ReplyAllRounded from "@mui/icons-material/ReplyAllRounded";
import { Link } from "react-router-dom";
export default function Update() {
  // id
  const [itsData, setData] = useState([]);
  // items >>
  const [price, setPrice] = useState("");
  const [card, setCard] = useState("");
  const [date, setDate] = useState("");
  const [time, setTime] = useState("");
  const [reason, setReason] = useState("");
  const [isBuy, setIsBuy] = useState(null);
  //  Checking PUT process >>
  const [putStatus, setPutStatus] = useState(false);
  // this is for UI process >>
  const [loader, setLoader] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  // Geting data from useParams
  // Geting json data and compile it to object >>
  const data = JSON.parse(decodeURIComponent(useParams().data));

  function fillState() {
    setPrice(data[1].price);
    setCard(data[1].card);
    setDate(data[1].date);
    setTime(data[1].time);
    setReason(data[1].reason);
    setIsBuy(data[1].isBuy);
  }

  // initial set data from line 18 >>
  useEffect(() => {
    setData(data);
    fillState();
  }, []);
  //   console.log(itsData);
  async function submitHandler(e) {
    e.preventDefault();
    setLoader(true);
    const info = {
      price,
      card,
      date,
      time,
      reason,
      isBuy,
    };
    try {
      const response = await put(`financeData/${itsData[0]}`, info);
      setPutStatus(response.ok);
    } catch (err) {
      console.log(err);
      setPutStatus(false);
    } finally {
      setLoader(false);
    }

    setShowAlert(true);
  }
  return (
    itsData.length && (
      <>
        {
          <div className="flex items-center w-full justify-center gap-3">
            {isBuy && <span className="green block">واریز</span>}
            <label className="switch my-5">
              <input
                onChange={(e) => setIsBuy(e.target.checked)}
                checked={isBuy}
                type="checkbox"
              />
              <span className="slider"></span>
              <span className="knob"></span>
            </label>
          {!isBuy &&<span className="red block">برداشت</span>}
          </div>
        }

        <form onSubmit={submitHandler} className="max-w-sm mx-auto p-5">
          <div className="mb-5">
            <label className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">
              ویرایش مبلغ{" "}
              {isBuy ? (
                <span className="green">واریز</span>
              ) : (
                <span className="red">برداشت</span>
              )}
            </label>
            {/* ___________________________________ */}
            <input
              onChange={(e) => setPrice(e.target.value)}
              value={price}
              type="text"
              className="shadow-xs bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-xs-light"
              placeholder="20,000"
              required
            />
          </div>
          <div className="mb-5">
            <label className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">
              ویرایش نام حساب
            </label>
            {/* ___________________________________ */}
            <input
              onChange={(e) => setCard(e.target.value)}
              value={card}
              type="text"
              className="shadow-xs bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-xs-light"
              required
              placeholder="تجارت"
            />
          </div>
          <div className="mb-5">
            <label className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">
              تاریخ
            </label>
            {/* ___________________________________ */}
            <input
              onChange={(e) => setDate(e.target.value)}
              value={date}
              type="text"
              className="shadow-xs bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-xs-light"
              required
              placeholder="25/10"
            />
          </div>
          <div className="mb-5">
            <label className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">
              ویرایش ساعت واریز
            </label>
            {/* ___________________________________ */}
            <input
              onChange={(e) => setTime(e.target.value)}
              value={time}
              type="text"
              className="shadow-xs bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-xs-light"
              required
              placeholder="ساعت 5 عصر"
            />
          </div>
          <div className="mb-5">
            <label className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">
              ویرایش دلیل{" "}
              {isBuy ? (
                <span className="green">واریز </span>
              ) : (
                <span className="red">برداشت </span>
              )}
            </label>
            {/* ___________________________________ */}
            <textarea
              onChange={(e) => setReason(e.target.value)}
              value={reason}
              id="message"
              rows="4"
              className="block p-2.5 w-full text-sm text-gray-900 bg-gray-50 rounded-lg border border-gray-300 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
              placeholder="خرید برای خانه"
            ></textarea>
          </div>

          <button
            type="submit"
            className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800"
          >
            ثبت
          </button>
        </form>
        <Modal
          open={showAlert}
          onClose={() => setShowAlert(false)}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
          className="flex justify-center items-center px-5"
        >
          <Box
            className={
              "bg-gray-800 max-w-sm p-5 rounded-lg flex justify-center items-center"
            }
          >
            <div className="text-xl text-center space-y-2">
              {!putStatus ? (
                <p className="red">
                  مشکلی پیش اومده لطفا به علیرضا اطلاع بده !
                </p>
              ) : (
                <>
                  <p className="green">
                    افرین مریم خانم اطلعات ویرایش و در جدول آمار نمایش داده شد !
                  </p>
                  <Link
                    to={"/"}
                    className=" text-gray-50 text-xs p-2 success rounded-lg inline-flex items-center gap-x-1"
                  >
                    برگشت به صفحه آمار
                    <ReplyAllRounded className="!w-5 !h-5" />
                  </Link>
                </>
              )}
            </div>
          </Box>
        </Modal>
      </>
    )
  );
}
