import React, { useEffect, useState } from "react";

export default function Loader({ isload, width }) {
  const [status, setStatus] = useState(false);
  const [myWidth, setWidth] = useState(false);
  useEffect(() => {
    setStatus(isload);
    setWidth(width);
  }, [isload, width]);
  return (
    <>
      {status && (
        <div
          style={{ width: !myWidth?"20%":myWidth }}
          className="left-0 fixed top-0  h-1 bg-blue-800 z-[1000] transition-[_2000ms_ease_all]"
        ></div>
      )}
    </>
  );
}
