import Insert from "./pages/form/Insert";
import Update from "./pages/form/Update";
import Home from "./Containers/HomeContainer";
import Animate from "./components/Animate";
let routes = [
  {
    path: "/",
    element: <Home />,
  },
  // report is shoud be (buy) or (sell)
  {
    path: "/form/:report",
    element: (
      <Animate>
        <Insert />
      </Animate>
    ),
  },
  {
    path: "/update/:data",
    element: (
      <Animate>
        <Update />
      </Animate>
    ),
  },
];
export default routes;
