import React, { useEffect, useState } from "react";
import { post } from "../../utils/methods";
import { Link, useParams } from "react-router-dom";
import { Modal } from "@mui/material";
import { Box } from "@mui/material";
import ReplyRoundedIcon from "@mui/icons-material/ReplyRounded";
import usePriceHandler from "../../utils/priceHandler";
export default function Form() {
  //inputValue is string-> example "20,000"
  //numericValue is int -> example 20000
  const { numericValue, inputValue, handleChange } = usePriceHandler();
  const [card, setCard] = useState("");
  const [date, setDate] = useState("");
  const [time, setTime] = useState("");
  const [reason, setReason] = useState("");
  const [statusPost, setStatusPost] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  const params = useParams().report;
  function cleaer() {
    if (statusPost) {
      setCard("");
      setDate("");
      setTime("");
      setReason("");
    }
  }

  async function submitHandler(e) {
    e.preventDefault();
    const buy_Or_sell = params == "buy" ? true : false;
    const info = {
      price:String(numericValue), // real price
      card,
      date,
      time,
      reason,
      isBuy: buy_Or_sell,
    };
    try {
      const result = await post("financeData", info);
      setStatusPost(result.ok);
    } catch (err) {
      console.log(err);
      setStatusPost(false);
    } finally {
      setShowAlert(true);
    }
  }
  useEffect(()=>{
    statusPost && cleaer()
  },[statusPost])
  return (
    <>
      <div
        className={`${
          params == "buy" ? "bodySuccess" : "bodyDanger"
        } p-5  h-screen`}
      >
        <div className="flex items-center gap-x-2 justify-between bg-gray-700 p-3 rounded-xl mb-5">
          <Link
            to={"/form/sell"}
            className={` ${
              params == "sell" && "danger"
            } block p-2 rounded-lg text-white`}
          >
            خرج کردم
          </Link>
          <Link
            to={"/form/buy"}
            className={` ${
              params == "buy" && "success"
            } block p-2 rounded-lg text-white`}
          >
            واریز داشتم
          </Link>
        </div>
        <form onSubmit={submitHandler} className="max-w-sm mx-auto">
          <div className="mb-5">
            <label className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">
              مبلغ{" "}
              {params == "buy" ? (
                <span className="green">واریز</span>
              ) : (
                <span className="red">برداشت</span>
              )}
            </label>
            {/* ___________________________________ */}

            <input
              onChange={handleChange}
              value={inputValue}
              type="text"
              className="shadow-xs bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-xs-light"
              placeholder="20,000"
              required
            />
          </div>
          <div className="mb-5">
            <label className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">
              {params == "buy" ? (
                <span className="green">واریز به</span>
              ) : (
                <span className="red">برداشت از </span>
              )}{" "}
              کدام حساب ؟
            </label>
            {/* ___________________________________ */}
            <input
              onChange={(e) => setCard(e.target.value)}
              value={card}
              type="text"
              className="shadow-xs bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-xs-light"
              required
              placeholder="تجارت"
            />
          </div>
          <div className="mb-5">
            <label className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">
              تاریخ
            </label>
            {/* ___________________________________ */}
            <input
              onChange={(e) => setDate(e.target.value)}
              value={date}
              type="text"
              className="shadow-xs bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-xs-light"
              required
              placeholder="25/10"
            />
          </div>
          <div className="mb-5">
            <label className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">
              ساعت
            </label>
            {/* ___________________________________ */}
            <input
              onChange={(e) => setTime(e.target.value)}
              value={time}
              type="text"
              className="shadow-xs bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-xs-light"
              required
              placeholder="ساعت 5 عصر"
            />
          </div>
          <div className="mb-5">
            <label className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">
              دلیل{" "}
              {params == "buy" ? (
                <span className="green">واریز </span>
              ) : (
                <span className="red">برداشت </span>
              )}
            </label>
            {/* ___________________________________ */}
            <textarea
              onChange={(e) => setReason(e.target.value)}
              value={reason}
              id="message"
              rows="4"
              className="block p-2.5 w-full text-sm text-gray-900 bg-gray-50 rounded-lg border border-gray-300 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
              placeholder="خرید برای خانه"
            ></textarea>
          </div>

          <button
            type="submit"
            className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800"
          >
            ثبت
          </button>
        </form>
      </div>
      <Modal
        open={showAlert}
        onClose={() => setShowAlert(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        className="flex justify-center items-center px-5"
      >
        <Box
          className={
            "bg-gray-800 max-w-sm p-5 rounded-lg flex justify-center items-center"
          }
        >
          <div className="text-xl text-center space-y-2">
            {!statusPost ? (
              <p className="red">مشکلی پیش اومده لطفا به علیرضا اطلاع بده !</p>
            ) : (
              <>
                <p className="green">
                  افرین مریم خانم اطلعات ارسال و در جدول آمار نمایش داده شد !
                </p>
                <Link
                  to={"/"}
                  className=" text-gray-50 text-xs p-2 success rounded-lg inline-flex items-center gap-x-1"
                >
                  برگشت به صفحه آمار
                  <ReplyRoundedIcon className="!w-5 !h-5" />
                </Link>
              </>
            )}
          </div>
        </Box>
      </Modal>
    </>
  );
}
