import React from "react";
import Table from "../../components/Table";
import DataArrayRoundedIcon from "@mui/icons-material/DataArrayRounded";
import Animate from "../../components/Animate";
import SignalWifiStatusbarConnectedNoInternet4RoundedIcon from "@mui/icons-material/SignalWifiStatusbarConnectedNoInternet4Rounded";
export default function Home({ data, dataStatus, loader, onDleteItem }) {
  if (!loader)
    // while loader is false >> data stils loading data
    return (
      <div className="w-full h-screen flex justify-center items-center text-3xl animate-pulse text-blue-300">
        در حال بارگزاری ...
      </div>
    );
  else
    return (
      <>
        {!dataStatus ? ( // if  dataStatus is false, request is not ok
          <>
            <div className="w-full h-screen flex justify-center items-center text-xl gap-x-2 animate-pulse text-red-300">
              خطا سرور
              <SignalWifiStatusbarConnectedNoInternet4RoundedIcon />
            </div>
          </>
        ) : !data.length ? ( //if length of data is 0, we don't have datas
          <div className="w-full h-screen flex justify-center items-center text-xl gap-x-2 animate-pulse text-blue-300">
            اطلاعاتی وجود ندارد
            <DataArrayRoundedIcon />
          </div>
        ) : (
          <Animate>

            <Table data={data} onDelete={onDleteItem} />
          </Animate>
          
        )}
      </>
    );
}
