import { useState } from "react";

const formatToIRR = (value) => {
  if (!value) return "";
  return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
};

const parseIRRToNumber = (value) => {
  if (!value) return 0;
  return Number(value.replace(/,/g, ""));
};

const useIranianCurrency = (initialValue = 0) => {
  const [numericValue, setNumericValue] = useState(initialValue);
  const [inputValue, setInputValue] = useState(formatToIRR(initialValue));

  const handleChange = (e) => {
    const rawValue = e.target.value.replace(/[^\d]/g, "");
    if (rawValue === "") {
      setInputValue("");
      setNumericValue(0);
      return;
    }
    const numeric = parseIRRToNumber(rawValue);
    setNumericValue(numeric);
    setInputValue(formatToIRR(rawValue));
  };

  return { numericValue, inputValue, handleChange };
};

export default useIranianCurrency;
