import React from "react";
import { Link } from "react-router-dom";
import ReplyRoundedIcon from "@mui/icons-material/ReplyRounded";
import CachedRoundedIcon from "@mui/icons-material/CachedRounded";
import { useLocation } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import ArrowBackRoundedIcon from '@mui/icons-material/ArrowBackRounded';
export default function Header() {
  const path = useLocation().pathname;
  const navigator = useNavigate();
  
  return (
    <>
      <header className="fixed z-10 inset-0 w-full bg-gray-950 h-[60px] justify-between flex gap-x-2 items-center px-1  md:px-4">
        {(path == "/" || path.startsWith("/update/")) && (
          <Link className="btn success text-sm" to={"/form/buy"}>
            ثبت گزارش
          </Link>
        )}
        {(path == "/form/buy" || path == "/form/sell" || path.startsWith("/update/")) && (
          <Link
            to={"/"}
            className=" text-gray-50 text-xs p-2 bg-gray-400/50 rounded-lg flex items-center gap-x-1"
          >
            برگشت به صفحه آمار
            <ReplyRoundedIcon className="!w-5 !h-5" />
          </Link>
        )}
        {path!="/"?(
          <button
            onClick={() => navigator(-1)}
            href=""
            className="block p-2 bg-gray-800 text-white rounded-xl "
          >
            <ArrowBackRoundedIcon/>
          </button>
        ):undefined}
      </header>
      <div className="h-[60px]"></div>
    </>
  );
}
